export default {
  data() {
    return {
      email: "",
      password: "",
    }
  },
  methods: {
    async onSubmit(e) {
      e.preventDefault()
      const payload = {
        data: {
          email: this.email,
          password: this.password,
        },
      }
      try {
        await this.$auth.loginWith("local", payload)
      } catch (error) {
        console.log(error)
      }
    },
  },
}
